body{
  background-color: hsl(75, 4%, 95%);
}
.app{
  display: flex;
  /* justify-content: center; */
  flex-direction: column;
  align-items: center;
  height: 100vh;
  width: 100%;
 
  background-color: hsl(75, 4%, 95%);
  /* margin-left: 400px; */
}
.todo{
  display: flex;
  justify-content: center;
  align-items: center;
}

.add
{

  color:hsl(0, 0%, 0%);
  font-size: 1.7rem;
  border: 0;
  background-color: transparent;
}
.add:hover{
  color: hsl(0,0%,0%);
}
.display{
  margin-bottom: 5px;
  border-bottom: 1px solid hsl(75, 4%, 60%);
  /* background-color: hsl(75, 4%, 80%); */
  padding: 5px 20px;
  /* display: flex;
  flex-direction: row; */
  /* border-radius: 5px; */
}
.display:hover{
  background-color: hsl(75, 4%, 90%);
}
.display li{
 font-size: 1.1rem;
  font-weight: 500;
  min-width: 200px;
}
.content{
  display: flex;
  align-items: center;
  justify-content: center;
}
.content .tick{
  /* color:hsl(203, 92%, 65%); */
  font-size: 1rem;
  border: 0;
  background-color: transparent;
  margin-right: 5px;
}

.content li{
  flex: 1;
}
.content1{
  display: flex;
  align-items: center;
  justify-content: center;
}
.content1 input{
  flex: 1;
  margin-bottom: 5px;
}
input{
  width: 100%;
  font-size: 1rem;
  padding: 5px;
  color: hsl(75, 4%, 35%);
}
.delete-button{
  margin-left: 10px;
  color:hsl(0, 88%, 49%);
  font-size: 1.7rem;
  background-color:hsl(75, 4%, 95%);
  border: 0;
  background-color: transparent;
 
}
.delete-button:hover{
  color: hsl(0, 88%, 42%);
}
.update-button{
  margin-left: 10px;
  color:hsl(120, 100%, 25%);
  font-size: 1.3rem;
  border: 0;
  background-color: transparent;
}
ul li{list-style-type: none;}
.update-button:hover{
  color: hsl(120, 100%, 20%);
}